import React, { useEffect, useState } from 'react';
import { useLiff } from 'react-liff';
import {Container, Navbar, Nav} from "react-bootstrap";
import {Button, Col, Row, Card} from "react-bootstrap";
import {Form} from "react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faList } from '@fortawesome/free-solid-svg-icons';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { faBell } from '@fortawesome/free-solid-svg-icons';
import {GapiConfig, useGoogleAuth, UserProfile} from 'gapi-oauth-react-hooks';

import './App.css';
import client from "./client_secret";

GapiConfig.setup(
    client.web.client_id,
    'https://www.googleapis.com/auth/spreadsheets',
    client.web.redirect_uris[1]
);

function SignedIn(){

}

function Loader() {
    return(
        <>
            <div className="loader text-center">
                <div className="loader-inner">
                    <div className="lds-roller mb-3">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                    <h4 className="text-uppercase font-weight-bold">Loading</h4>
                </div>
            </div>
        </>
    );
}

function App() {
    const [showFunc, setShowFunc] = useState({
        List: true,
        Add: false,
        Subscription: false
    });
    const [profile, setProfile] = useState({
        "userId": "",
        "displayName": "",
        "pictureUrl": "",
        "statusMessage": "",
    });
    const [remind, setRemind] = useState({});
    const [type, setType] = useState({});
    const { error, liff, isLoggedIn, ready } = useLiff();
    const auth = useGoogleAuth();
    const [sheetId] = useState('1xyVAC8Rby3n_s5NERM2_8uPYic11YlkycDM_TknOgTs');
    const [key] = useState('AIzaSyAYfx0INy81MTWwUDbGb1iPN88g8_lnoDc');

    useEffect(() => {
        if (!isLoggedIn) return;
        (async () => {
            const profile = await liff.getProfile();
            setProfile({
                "userId": profile.userId,
                "displayName": profile.displayName,
                "pictureUrl": profile.pictureUrl,
                "statusMessage": profile.statusMessage,
            });
        })();
    }, [liff, isLoggedIn]);

    function GLoggedIn() {
        return (
            <>
                <Card>
                    <Card.Body>
                        <Card.Title>Google</Card.Title>
                    </Card.Body>
                    <Card.Footer>
                        <div className="d-grid gap-2">
                            <Button variant="primary" onClick={auth.onSignIn}>Login</Button>
                        </div>
                    </Card.Footer>
                </Card>
            </>
        );
    }

    function LoggedIn(){
        return (
            <>
                <Card>
                    <Card.Body>
                        <Card.Img variant="top" src={'./images/logo.png'} />
                    </Card.Body>
                    <Card.Footer>
                        <div className="d-grid gap-2">
                            <Button variant="primary" onClick={liff.login}>Login</Button>
                        </div>
                    </Card.Footer>
                </Card>
            </>
        );
    }

    function AddRemind() {
        const [validated, setValidated] = useState(false);
        const [data, setData] = useState({
            type: "",
            title: "",
            message: "",
            date: new Date().toString(),
        });

        const handleChange = (e) =>{
            setData({ ...data, [e.target.name]: e.target.value });
        };
        const handleSubmit = (event) => {
            const form = event.currentTarget;
            event.preventDefault();
            event.stopPropagation();
            setValidated(true);
            if (form.checkValidity()) {
                fetch(
                    "https://sheets.googleapis.com/v4/spreadsheets/"+sheetId+"/values/{range}:append",
                    {
                        method: "POST",
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify(data),
                    }
                )
                .then(res => res.json())
                .then(data => {
                })
                .catch(e => {
                    console.error(e);
                });
            }
        };
        console.log(auth.signedUser)
        if (auth.state == 'Errored') return <div></div>;
        if (!ready || auth.state == 'Loading') return <Loader/>;
        // if (!isLoggedIn) return <LoggedIn/>;
        if (auth.state == 'NotSignedIn') return auth.onSignIn();
        // if (auth.state == 'SignedIn') return SignedIn: <SignedIn {...auth} />;
        return (
            <Row>
                <Col sm={12} className="mb-2">
                    <Form noValidate validated={validated} onSubmit={handleSubmit}>
                        <Card className="shadow-sm">
                            <Card.Header as="h5" className="bg-secondary text-light"></Card.Header>
                            <Card.Body>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Control as="select" name="type" onChange={handleChange} required>
                                        <option value=""></option>
                                        <option value="E"></option>
                                        <option value="G"></option>
                                        <option value="S"></option>
                                    </Form.Control>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Select name="title" onChange={handleChange} required>
                                        <option value=""></option>
                                        <option value="USD">USD</option>
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Select name="condition" onChange={handleChange} required>
                                        <option value=""></option>
                                        <option value="P"></option>
                                    </Form.Select>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Control type="number" name="high" onChange={handleChange} required />
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Control type="number" name="low" onChange={handleChange} required />
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Select name="time_s" onChange={handleChange} required>
                                        <option value=""></option>
                                        {[...Array(24)].map((x, i) =>
                                            <option value={i}>{i}</option>
                                        )}
                                    </Form.Select>
                                    <Form.Text muted>
                                        00-24
                                    </Form.Text>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Select name="time_e" onChange={handleChange} required>
                                        <option value=""></option>
                                        {[...Array(24)].map((x, i) =>
                                            <option value={i}>{i}</option>
                                        )}
                                    </Form.Select>
                                    <Form.Text muted>
                                        00-24
                                    </Form.Text>
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Form.Label></Form.Label>
                                    <Form.Select name="cycle" onChange={handleChange} required>
                                        <option value=""></option>
                                        {[...Array(59)].map((x, i) =>
                                            <option value={i+1}>{i+1}</option>
                                        )}
                                    </Form.Select>
                                    <Form.Text muted>
                                         1-59 
                                    </Form.Text>
                                </Form.Group>
                            </Card.Body>
                            <Card.Footer>
                                <Row>
                                    <Col className="d-grid gap-2">
                                        <Button type="submit" variant="primary" className="rounded-pill"></Button>
                                    </Col>
                                </Row>
                            </Card.Footer>
                        </Card>
                    </Form>
                </Col>
            </Row>
        );
    }

    function RemindList() {
        const [isLoading, setIsLoading] = useState(true);
        const [isError, setIsError] = useState(false);
        useEffect(() => {
            const fetchType = () =>{
                fetch('https://sheets.googleapis.com/v4/spreadsheets/' + sheetId + '/values/sheet2!A2:B10?key=' + key,
                    {
                        method: "GET",
                        headers: new Headers({
                            'Content-Type': 'application/json',
                        })
                    })
                    .then(res => res.json())
                    .then(data => {
                        setIsLoading(false);
                        setType(data.values);
                        fetchData();
                        console.log('type', type)
                    })
                    .catch(e => {
                        setIsLoading(false);
                        setIsError(true);
                        console.error(e);
                    });
            };
            const fetchData  = () =>{
                fetch('https://sheets.googleapis.com/v4/spreadsheets/' + sheetId + '/values/sheet1!A2:L1000?key=' + key,
                    {
                        method: "GET",
                        headers: new Headers({
                            'Content-Type': 'application/json',
                        })
                    })
                    .then(res => res.json())
                    .then(data => {
                        setIsLoading(false);
                        setRemind(data.values.filter(function (item, index, array) {
                            return item[1] == profile.userId;
                        }));
                        console.log('remind', remind)
                    })
                    .catch(e => {
                        setIsLoading(false);
                        setIsError(true);
                        console.error(e);
                    });
            };
            fetchType();
        }, []);
        if (auth.state == 'Errored') return <div></div>;
        if (isLoading || !ready || auth.state == 'Loading') return <Loader/>;
        if (!isLoggedIn) return <LoggedIn/>;
        if (auth.state == 'NotSignedIn') return <GLoggedIn/>;
        if (isError) return <div>Error</div>;
        if (remind.length === 0) return <div>No any remind</div>;
        return(
            <Row>
                {remind.map((obj, i) =>(
                    <Col md={6} sm={12} className="mb-2" key={i}>
                        <Card className="bg-white border-white border-0 shadow-sm">
                            <div className="card-custom-img" />
                            <div className="card-custom-avatar">
                                <img src={'./images/' + obj[4] + (obj[4] == 'gold' ? '.png':'.svg')} alt={obj[4]} className="img-fluid"/>
                            </div>
                            <Card.Body>
                                <Row>
                                    <Col></Col>
                                    <Col>{type.find(function (item, index, array){
                                        return item[0] === obj[2];
                                    })[1]}
                                    </Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{type.find(function (item, index, array){
                                        return item[0] === obj[3];
                                    })[1]}
                                    </Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{obj[4]}</Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{obj[5]}</Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{obj[6]}</Col>
                                </Row>
                                <Row>
                                    <Col></Col>
                                    <Col>{obj[7]} {obj[8]} {obj[9]}</Col>
                                </Row>
                            </Card.Body>
                            <Card.Footer style={{background: 'inherit', borderColor: 'inherit'}}>
                                <Row>
                                    <Col className="d-grid gap-2">
                                        <Button variant="warning" className="rounded-pill"></Button>
                                    </Col>
                                    <Col className="d-grid gap-2">
                                        <Button variant="danger" className="rounded-pill"></Button>
                                    </Col>
                                </Row>
                            </Card.Footer>
                        </Card>
                    </Col>
                ))}
            </Row>
        );
    }

    return (
        <Container>
            <Row className="mb-5">
                <Col className="mb-5">
                    {/*{showFunc.List && <RemindList/>}*/}
                    {showFunc.Add && <AddRemind/>}
                    {showFunc.Subscription && <div>My Subscription</div>}
                </Col>
            </Row>
            <Navbar fixed="bottom" bg="light">
                <Container fluid>
                    <Col sm={4} className="text-center">
                            <Nav.Link onClick={() => setShowFunc({
                                List: true,
                                Add: false,
                                Subscription: false
                            })}>
                                <FontAwesomeIcon icon={faList} /><br />
                                RemindList
                            </Nav.Link>
                    </Col>
                    <Col sm={4} className="text-center">
                        <Nav.Link onClick={() => setShowFunc({
                            List: false,
                            Add: true,
                            Subscription: false
                        })}>
                            <FontAwesomeIcon icon={faPlus} /><br />
                            AddRemind
                        </Nav.Link>
                    </Col>
                    <Col sm={4} className="text-center">
                        <Nav.Link onClick={() => setShowFunc({
                            List: false,
                            Add: false,
                            Subscription:true
                        })}>
                            <FontAwesomeIcon icon={faBell} /><br />
                            Subscription
                        </Nav.Link>
                    </Col>
                </Container>
            </Navbar>
        </Container>
    );
}

export default App;
